}

.modal-overlay {
    background-color: rgba(0, 0, 0, 0.7);
    bottom: 0;
    left: 0;
    position: fixed;
    right: 0;
    top: 0;
    z-index: 1000;
}

.closed .modal {
    top: -50%;
}

.closed .modal-overlay {
    display: none;
}

.modal {
    box-shadow: 0 12px 15px 0 rgba(0, 0, 0, 0.22), 0 17px 20px 0 rgba(0, 0, 0, 0.12);
    color: #f2f2f2;
    background-color: #2C2F34;
    max-height: calc(100% - 10em);
    overflow-y: auto;
    position: fixed;
    top: 5em;
    left: 50%;
    -webkit-transform: translate(-50%, 0%);
            transform: translate(-50%, 0%);
    z-index: 1100;
    -webkit-transition: all .5s ease;
    transition: all .5s ease;
    border-radius:4px;
    display: block;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: 500px;
    padding-bottom:10px;
}

.modal .title {
    background-color: red;
    text-align: center;
    color: white;
    line-height: 40px;
}

.modal .title .right-align {
    position: absolute;
    right: 5px;
}

.modal .title .right-align, .modal .title .right-align i {
    line-height: 40px;
}

.modal .title .right-align:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.modal .body {
    padding: 1.2em;
}

.modal-center {
    display: inline-block;
    width: 75%;
}
.auth-input {
    width: 100%;
    margin-bottom: 10px;
    background-color: #191919;
    color: #f2f2f2;
    border: 1px solid transparent;
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, .1);
    box-sizing: border-box;
    border-radius: 5px;
    padding-left: 10px;
}

.auth-form {
    width: 100%;
    display: inline-block;
}

.auth-submit {
    float: right;
}

.auth-error {
    color: red; 
    text-align: left;
    font-size: 13px;
    margin-bottom: 0px;
}
.toaster-background-success {
    color: #fff;
    background-color: #5cb85c;
    border-color: #4cae4c;
}

.toaster-background-error {
    color: #fff;
    background-color: #d9534f;
    border-color: #d43f3a;
}

.toaster {
    position: fixed;
    top: 20px;
    right: 20px;
    border-radius: 3px;
    box-shadow: 0 0 4px 0 black;
    z-index: 1001;
}

.toaster-body {
    display: inline-flex;
}

.toaster-text {
    padding: 12px 12px 12px 22px;
}

.toaster-close {
    cursor: pointer;
    display: flex;
    align-items: center;
    padding: 0 10px;
}

.uploader-backgroundColor {
